import React, {Component} from "react";
import PropTypes from "prop-types";
import {Link} from "react-router-dom";

class FollowersDisplay extends Component {
	constructor(props = {}) {
		super(props);
		this.scrollRef = React.createRef();
	}

	getFollowers = (e = {}) => {
		const {getGitHubFollowers} = this.props;
		const {username} = e.nativeEvent.target.dataset;
		username && getGitHubFollowers(e, username);
	};

	getUser = (e = {}) => {
		const {findGitHubUser} = this.props;
		const {username} = e.nativeEvent.target.dataset;
		username && findGitHubUser(e, username);
	};

	render() {
		if(this.scrollRef.current) {
			this.scrollRef.current.scrollTop = 0;
		}

		const {
			user: {
				id:userID,
				avatar_url,
				username,
				followers = [],
			} = {}
		} = this.props;

		const followersDisplay = followers.length && followers.map((item = {}) => {
			return (
				<div className="avatar" key={`follower_${item.id}`}>
					<Link key={`link_follower_${item.id}`} to={`/${item.login}`}>
						<img src={item.avatar_url}
						 	alt={item.avatar_url}
						 	data-username={`${item.login}`}
						 	title={`follower ${item.login}`} />
						<div className="user-name" data-username={`${item.login}`}>{item.login}</div>
					</Link>
				</div>
			)
		});

		const userDisplay = username && (
			<div className="avatar" key={userID}>
				<Link key={`link_follower_${username}`} to={`/${username}`}>
					{avatar_url && <img src={avatar_url} alt={avatar_url} data-username={`${username}`} title={`user ${username}`}></img>}
					<div className="user-name" data-username={`${username}`}>{username}</div>
				</Link>
			</div>
		);
		const defaultDisplay = (<div>No GitHub User</div>);
		const element = (followersDisplay || userDisplay || defaultDisplay);
		const parent = (
			followersDisplay? <div id="followers-display" ref={this.scrollRef} onClick={this.getUser}>{element}</div> :
			userDisplay? <div id="followers-display" ref={this.scrollRef} onClick={this.getFollowers}>{element}</div> :
			<div id="followers-display">{element}</div>
		);

		return (parent);
	}
}

FollowersDisplay.propTypes = {
	user: PropTypes.object,
	findGitHubUser: PropTypes.func,
	getGitHubFollowers: PropTypes.func
};

FollowersDisplay.defaultProps = {
	user: {},
	findGitHubUser: (e) => {},
	getGitHubFollowers: (e) => {}
};

export default (FollowersDisplay);
