const root = 'https://api.github.com/';

let indexDefault = 1;
let usernameDefault = '';
let perPageDefault = 10;

export const getIndex = () => {
	return indexDefault;
};

const reset = ({username = '', perPage = 10, page = 1} = {}) => {
	indexDefault = page;
	usernameDefault = username;
	perPageDefault = perPage;
};

const nextPage = () => {
	indexDefault++;
};

export const getGitHubUser = (username = '') => {
	reset({
		username,
		perPage: 10,
		page: 1
	});
	return new Promise((resolve, reject) => {
		fetch(`${root}users/${username}`).then((response = {}) => {
			return response.json && response.json();
		}).then(json => {
			resolve(json);
		}).catch(error => {
			reject(error);
		});
	});
};

export const getGitHubUserFollowers = ({username = '', perPage = perPageDefault, page = 1} = {}) => {
	reset({
		username,
		perPage,
		page
	});

	return new Promise((resolve, reject) => {
		fetch(`${root}users/${username}/followers?per_page=${perPage}&page=${page}`).then((response = {}) => {
			return response.json && response.json();
		}).then(json => {
			if(Array.isArray(json) === false) {
				throw new Error('invalid')
			} else {
				resolve(json);
			}
		}).catch(error => {
			reject(error);
		});
	})
};

export const getMoreUsers = ({perPage = perPageDefault} = {}) => {
	nextPage();
	return new Promise((resolve, reject) => {
		fetch(`${root}users/${usernameDefault}/followers?per_page=${perPage}&page=${indexDefault}`).then(response => {
			return response.json && response.json();
		}).then(json => {
			resolve(json);
		}).catch(error => {
			reject(error);
		});
	})
};
