import React, {Component} from 'react';
import {getGitHubUser, getGitHubUserFollowers, getMoreUsers, getIndex} from './Services';
import FindGitHubUser from "./components/FindGitHubUser";
import FollowersDisplay from "./components/FollowersDisplay";
import {isEqual} from "lodash";

export default class App extends Component {
    constructor(props = {}) {
        super(props);

        const {username = ''} = this.props.match.params;

        this.state = {
            index: getIndex(),
            showFollowers: false,
            user: {
                id:0,
                username,
                followers: [],
                followerCount: 0
            }
        };
    };

    componentDidMount() {
        const {username = ''} = this.props.match.params;
        username && this.findGitHubUser('', username);
    }

    findGitHubUser = (e = {}, followerName = '') => {
        const {history} = this.props;
        const {user: {username, followers, id} = {}} = this.state;

        history.push(`/${followerName || username}`);
        getGitHubUser(followerName || username).then((user = {}) => {
            this.setState({
                showFollowers: false,
                index: getIndex(),
                user: {
                    ...user,
                    username: followerName || username,
                    followerCount: user.followers,
                    followers: id === user.id ? followers : [],
                },
            });
        }).catch(error => {
            console.error(error);
        });
    };

    findGitHubUserChange = (e = {}) => {
        const {value} = e.target;
        const user =  {
            username : value,
            followers: [],
            followerCount: 0
        };

        this.setState({
            user
        });
    };

    getGitHubFollowers = (e = {}, selectedusername = '') => {
        const {user: {username, followerCount} = {}} = this.state;

        getGitHubUserFollowers({username: selectedusername || username}).then((followers = []) => {
            this.setState({
                showFollowers: true,
                index: getIndex(),
                user: {
                    ...this.state.user,
                    followers,
                    followerCount
                }
            });
        }).catch(error => {
            console.error(error);
        });
    };

    getNext = (e = {}) => {
        const {user: {followers = [], followerCount} = {}} = this.state;

        getMoreUsers().then((moreFollowers = []) => {
            this.setState({
                index: getIndex(),
                user: {
                    ...this.state.user,
                    followers: [...moreFollowers, ...followers],
                    followerCount
                }
            });
        }).catch(error => {
            console.error(error);
        });
    };

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return !isEqual(nextState.user, this.state.user)
    }

    render() {
        const {showFollowers, index, user: {
            username,
            followers,
            followerCount
        } = {}} = this.state;

        const {user = {}} = this.state;
        const  title = showFollowers? 'Followers' : 'Github User'

        return (
            <div className="App">
                <div className="App-header">
                    {title}
                    <FollowersDisplay user={user}
                                      findGitHubUser={this.findGitHubUser}
                                      getGitHubFollowers={this.getGitHubFollowers}
                    />
                </div>
                <main className="App-main">
                    <div id="result">
                        <div>handle: {username}</div>
                        <div>followers displayed: {followers.length}</div>
                        <div>total followers: {followerCount}</div>
                    </div>
                    <FindGitHubUser
                        index={index}
                        user={this.state.user}
                        findGitHubUser={this.findGitHubUser}
                        getGitHubFollowers={this.getGitHubFollowers}
                        getNext={this.getNext}
                        findGitHubUserChange={this.findGitHubUserChange} />
                </main>
            </div>
        );
    }
}
