import React from "react";
import PropTypes from "prop-types";
import enums from '../enums';

const {minUserLength} = enums;
let flag = false

const FindGitHubUser = (props = {}) => {
	const {
		user: {
			followerCount,
			followers,
			username
		},
		findGitHubUser,
		findGitHubUserChange,
		getGitHubFollowers,
		getNext
	} = props;

	return (
		<div className="content">
			<form onSubmit={(e = {}) => {
				e.nativeEvent.preventDefault();
				if(flag){
					flag = false;
					return;
				}
				if(username.length > minUserLength) {
					findGitHubUser();
				}
			}}>
				<input id="findGithubUser"
					   type="text"
					   value={username}
					   autoFocus
					   onChange={findGitHubUserChange}
					   placeholder="GitHub User" />
				{username.length >= minUserLength && <button key="findGitHubUser_btn" onClick={(e = {}) => {
					flag = true;
					findGitHubUser();
				}}>
					Find GitHub User
				</button>}
				{followerCount > 0 && <button key="getGitHubFollowersClick_btn" onClick={(e = {}) => {
					flag = true;
					getGitHubFollowers();
				}}>
					Find GitHub User Followers
				</button>}
				{(followers.length > 0 && followerCount > followers.length) && <button key="getNext_btn" onClick={(e = {}) => {
					flag = true;
					getNext();
				}}>
					Load More...
				</button>}
			</form>
		</div>
	)
};

FindGitHubUser.propTypes = {
	index:PropTypes.number,
	user: PropTypes.object,
	findGitHubUser: PropTypes.func,
	findGitHubUserChange: PropTypes.func,
	getGitHubFollowers: PropTypes.func,
	getNext: PropTypes.func
};

FindGitHubUser.defaultProps = {
	index : -1,
	user: {},
	findGitHubUser: (e = {}) => {},
	findGitHubUserChange: (e = {}) => {},
	getGitHubFollowers: (e = {}) => {},
	getNext: (e = {}) => {}
};

export default FindGitHubUser
